<template>
    <div class="show-message">
        <div v-if="$route.name === '消息管理'">
            <Button type="success" size="large" to="/manage/msg_view/guanli/create" class="create">添加客服消息</Button>
            <Table class="message-table" :columns="messageHeader" :data="messageData"></Table>
            <Page style="margin-top:30px;" show-total :total="dataList.length" :page-size="10" @on-change="changePage" />
        </div>
        <router-view />
    </div>
</template>

<script>
export default {
  data() {
    return {
      dataList: [],
      messageData: [],
      messageHeader: [
        {
          key: "type",
          title: "类型",
          align: "center",
          render: (h, params) => {
            let type =
              (params.row.type == 1 ? "文本消息" : "图文消息") +
              (params.row.contents.length > 1 ? "(多)" : "");
            return h(
              "span",
              {
                props: {}
              },
              type
            );
          }
        },
        {
          key: "task",
          title: "是否延时",
          align: "center",
          render: (h, params) => {
            let task = params.row.task == true ? "是" : "否";
            return h(
              "span",
              {
                props: {}
              },
              task
            );
          }
        },
        {
          key: "delay",
          title: "延时时间(分钟)",
          align: "center",
          render: (h, params) => {
            let delay = params.row.task == true ? params.row.delay : "——";
            return h(
              "span",
              {
                props: {}
              },
              delay
            );
          }
        },
        {
          key: "is_timing",
          title: "是否定时(分钟)",
          align: "center",
          render: (h, params) => {
            let is_timing = params.row.is_timing == true ? "是" : "否";
            return h(
              "span",
              {
                props: {}
              },
              is_timing
            );
          }
        },
        {
          key: "timing_time",
          title: "定时时间",
          align: "center",
          width: 100,
          render: (h, params) => {
            let timing_time =
              params.row.is_timing == true ? params.row.time : "——";
            return h(
              "span",
              {
                props: {}
              },
              timing_time
            );
          }
        },
        {
          key: "sex",
          title: "性别",
          align: "center",
          width: 100,
          render: (h, params) => {
            let sex =
              params.row.sex == "0"
                ? "未知"
                : params.row.sex == "1"
                  ? "男"
                  : params.row.sex == "2"
                    ? "女"
                    : "全部";
            return h(
              "span",
              {
                props: {}
              },
              sex
            );
          }
        },
        {
          key: "title",
          title: "标题",
          align: "center",
          tooltip: true,
          render: (h, params) => {
            let title = params.row.contents[0].title || "——";
            return h(
              "Tooltip",
              {
                props: {
                  placement: "top-start",
                  content: title,
                  maxWidth: 200
                }
              },
              [
                h(
                  "span",
                  {
                    style: {
                      diaplay: "inline-block",
                      width: "100%",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      wordBreak: "break-all"
                    }
                  },
                  title
                )
              ]
            );
          }
        },
        {
          key: "description",
          title: "详细信息",
          align: "center",
          tooltip: true,
          width: 90,
          render: (h, params) => {
            let description = params.row.contents[0].description || "——";
            return h(
              "Tooltip",
              {
                props: {
                  placement: "top-start",
                  content: description,
                  maxWidth: 200
                }
              },
              [
                h(
                  "span",
                  {
                    style: {
                      diaplay: "inline-block",
                      width: "100%",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      wordBreak: "break-all"
                    }
                  },
                  description
                )
              ]
            );
          }
        },
        {
          key: "url",
          title: "链接",
          align: "center",
          tooltip: true,
          render: (h, params) => {
            let url = params.row.contents[0].url || "——";
            return h(
              "Tooltip",
              {
                props: {
                  placement: "top-start",
                  content: url,
                  maxWidth: 200
                }
              },
              [
                h(
                  "span",
                  {
                    style: {
                      diaplay: "inline-block",
                      width: "100%",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      wordBreak: "break-all"
                    }
                  },
                  url
                )
              ]
            );
          }
        },
        {
          key: "img",
          title: "图片",
          width: 160,
          align: "center",
          render: (h, params) => {
            return h("img", {
              attrs: {
                src: params.row.img,
                style: "width: 100%; padding: 10px;"
              }
            });
          }
        },
        {
          title: "Action",
          align: "center",
          width: 250,
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "warning",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.copyMessage(params.row);
                    }
                  }
                },
                "复制"
              ),
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "修改客服消息",
                        params: params.row
                      });
                    }
                  }
                },
                "修改"
              ),
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px",
                    display:
                      !params.row.task && !params.row.is_timing
                        ? "inline-block"
                        : "none"
                  },
                  props: {
                    type: "success",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.sendMessage(params.row);
                    }
                  }
                },
                "发送消息"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.deleteMessage(params.row);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showMessage();
  },
  methods: {
    copyMessage(row) {
      this.$axios
        .post("/message/create", {
          contents: row.contents,
          type: row.type,
          task: row.task,
          delay: row.delay,
          is_timing: row.is_timing,
          timing_time: row.timing_time,
          codes: row.codes,
          img: row.img,
          tagId: row.tagId
        })
        .then(res => {
          if (res.data.success) {
            this.$Message.info(res.data.success);
            this.messageData.unshift(res.data.data);
            // window.location.href = '/msg_view/manage'
          } else {
            this.$Message.info(res.data.err);
          }
        });
    },
    showMessage() {
      this.$axios("/message").then(res => {
        if (res.data.messages.length != 0) {
          this.dataList = res.data.messages;
          this.messageData = this.dataList.slice(0, 10)
        }
      });
    },
    sendMessage(row) {
      this.$Modal.confirm({
        title: "提示:",
        content: "确认发送这条消息吗？",
        onOk: () => {
          this.$axios
            .get("/message/send", {
              params: {
                id: row._id,
                take_over: true,
                tagId: row.tagId
              }
            })
            .then(res => {
              this.$Message.info(res.data.success);
            });
        }
      });
    },
    deleteMessage(row) {
      this.$Modal.confirm({
        title: "提示:",
        content: "确认删除这条内容吗？",
        onOk: () => {
          this.$axios
            .get("/message/delete", { params: { id: row._id } })
            .then(res => {
              if (res.data.data == 0) {
                this.$Message.info("已删除最后一条数据");
                this.messageData = [];
              } else {
                this.$Message.info(res.data.success);
                this.showMessage();
              }
            });
        }
      });
    },
    changePage(index){
      var _start = ( index - 1 ) * 10;
      var _end = index * 10;
      this.messageData = this.dataList.slice(_start,_end);
      this.goTop()
    },
    goTop () {
      let self = this
      self.timer = setInterval(function () {
        let osTop = document.documentElement.scrollTop || document.body.scrollTop
        let ispeed = Math.floor(-osTop)
        document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
        self.isTop = true
        if (osTop === 0) {
          clearInterval(self.timer)
        }
      }, 30)
    }
  },
  watch: {
    $route(to, from) {
      if (from.name == "添加客服消息" || from.name == "修改客服消息") {
        this.showMessage();
      }
    }
  }
};
</script>

<style scoped>
.create {
  margin-bottom: 30px;
}
</style>